  	const state = {
  		/*刷新后可以从本地存储里拿原理的地方，而不是一刷新就又变成北京了+*/
  		nm : window.localStorage.getItem('nowNM') || '北京',
  		id : window.localStorage.getItem('nowID') || 1
  	};

  	const actions = {

  	};

  	const mutations = {
  		/*用大写易于让人知道是状态管理的方法*/
  		/*payload传过来的载荷*/
  		CITY_INFO(state , payload){
  			state.nm = payload.nm;
  			state.id = payload.id;
  		}
  	};

  	export default {
  		namespaced : true,
  		state,
  		actions,
  		mutations
  	}


