<template>
	<div>
		<footer id="miaoFooter">
			<ul>
				<!--router-link将拦截点击事件，以便浏览器不会尝试重新加载页面，在@click后加native能解决-->
				<router-link tag="li" to="/sousuo">
					<span>
						❂
				    </span>
				    <p>电影</p>
			    </router-link>
			    <router-link tag="li" to="/movielu">
			    	<span>   
			    		▣
			    	</span>
			    	<p>TOP榜</p>
			    </router-link>
			    <router-link tag="li" to="/stou">
			    	<span>
			    		♥
			    	</span>
			    	<p>我的</p>
			    </router-link>
			</ul>
		</footer>
	</div>
</template>
<script>

	export default{
		name:"pag",
		data(){
			return{
				
			}
		}
		/*自动点击事件，在页面初始化后自动点击*/
		/*directives:{
			clickDown:{
				inserted(el){
					el.click()
				}
			}
		},*/
	}
</script>
<style scoped>
	*{
		margin:0;
		padding: 0;
	}
	#miaoFooter{
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 50px;
	}
	ul li {
		list-style-type: none;
	}
	#miaoFooter ul{
		background-color: white;
		display: flex;
		/*盒内元素从左向右排列居中*/
		align-items: center;
		height: 50px;
		text-align: center;
	}
	#miaoFooter ul li{
		/*让弹性盒子里每个元素拥有相同的大小*/
		flex: 1;
		height: 40px;
	}

	#miaoFooter ul li span{
		font-size: 20px;
	}
	#miaoFooter ul li p{
		font-size: 12px;
	}
	#miaoFooter ul li.router-link-active{
		color: #f03d23;
	}

</style>


