// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Router from 'vue-router'
import {store} from './store/store'
import movielu from "./components/movielu"
import axios from "axios"
import VueJsonp from "vue-jsonp"
import VueResource from "vue-resource"
import load from "./components/san/loading"
Vue.component('load',load);

/*全局注册子组件，然后把better-scroll用到这个组件中*/
import bscroll from "./components/san/betterScroll"
Vue.component('bscroll',bscroll);
Vue.use(VueJsonp);
Vue.use(VueResource);

Vue.prototype.axios = axios;

Vue.config.productionTip = false

Vue.filter('setWH',(url , arg) =>{
	//选取图片路径里的W,H，然后用传来的值取代
	return url.replace(/w\.h/,arg);
});

Vue.filter('ooo',(long) => {
	/*先用if确定一下是否有数据，不然会出现报错 Cannot read property ‘length’ of undefined*/
	if(long){
	 	if(long.length > 16){
	 	   return long.slice(0,16)+'...'
	 	}
	 }
	 	return long
})

Vue.filter('len',(long) => {
	/*先用if确定一下是否有数据，不然会出现报错 Cannot read property ‘length’ of undefined*/
	if(long){
	 	if(long.length > 10){
	 	   return long.slice(0,10)+'...'
	 	}
	 }
	 	return long
})

Vue.filter('jion',(dat)=>{
	return dat.join(' , ');
})

Vue.use(Router);

const router = new Router({
	routes:[
		{path:'/',redirect:'/sousuo'},
		{
			path:"/sousuo",
			/*按需加载路由*/
			component:() => import("@/components/sousuo"),
			children:[
			/*二级路由高亮问题https://blog.csdn.net/bingot/article/details/86476646*/
			/*平时用子路由可以不用在path里加上父路径，但是如果想让子路由
			能跟父路由能同一起高亮（给激活的路由添加样式）就得这样写*/
				{path:"/sousuo/redir",component:() => import("@/components/san/redir")},
				{path:"/",component:() => import("@/components/san/andi")},
				{path:"/sousuo/zhudi",component:() => import("@/components/san/zhudi")},
			//	{path:"/sousuo/lenye",component:() => import("@/components/san/lenye")},
				/*一个页面多个router-view的建法*/
				/*用props来传递id，如果不是多个view视图可以直接写props：true就行*/
				{path:"detail/:setid",
				components:{detail:() => import("@/components/san/detail")},
				props:{detail:true}}
			]
		},
		{
			/*在路径后面加个:id才能在mounted里通过this.$route.path获取当前激活的路由,
			但在path后面加：id就必须在router-link里用:to='路径 + 传值'，
			必须有值不然显示页面不出来*/

			path:"/movielu",
			name:"wowowo",
			component:() => import("@/components/movielu")
		},
		{
			path:"/stou",
			name:"tatata",
			component:() => import("@/components/stou")
		}
	],
	mode:"hash"
})

new Vue({
  store:store,
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



