import Vue from 'vue'
import Vuex from 'vuex'
import city from './city'

Vue.use(Vuex)

export const store = new Vuex.Store({
	state:{
		/*这里放全局参数*/
	},
	mutations:{
		/*这里放set方法*/
		
	},
	//通过触发actions再来触发mutations
	//actions可以包含任意异步操作
	actions:{
		
	},
	getters:{
		/*这里用get方法*/
	},
	modules:{
		/*引入子状态管理文件文件，有助于模块化开发,这里引入city*/
		city
	}
});


