<template>
	<div ref="Bscroll" class="warrp">
		<!--slot是vue中的插槽-->
		<slot></slot>
	</div>
</template>
<script>
	import betterScroll from "better-scroll"
	export default{
		name:"bscroll",
		data(){
			return{

			}
		},
		props:{
			toscroll:{
				type:Function,
				default:function(){}
			},
			touchend:{
				type:Function,
				default:function(){}
			}
		},
		mounted(){
			this.$nextTick(()=>{	
				if(!this.scroll){
				this.scroll = new betterScroll(this.$refs.Bscroll , {
					tap:true,
					probeType:1
				//	mouseWheel: true,//开启鼠标滚轮
				//	disableMouse: false,//启用鼠标拖动
				//	disableTouch: false//启用手指触摸
				});
				}else{
					this.scroll.refresh();
					
				}
				
				//this.scroll = scroll;

				this.scroll.on('scroll' ,(pos) =>{
					this.toscroll(pos);
				});

				this.scroll.on('touchEnd',(pos) =>{
					this.touchend(pos);
				});
			})
			
		},
		methods:{
			toScrollTop(y){
				//跟城市列表里的点击事件一起的
				//better里的scrollTo方法
				this.scroll.scrollTo(0,y);
			}
		}
	}
</script>
<style scoped>
	/*要让其高度为100%才行*/
	.warrp{
		height: 100%;
	}
</style>


