<template>
	<div class="crui">
		<hello title="TOP电影榜"></hello>
    	<pag></pag>
		<div class="baib">
			<load v-if="istrue"></load>
			<!--better-scroll里计算高度是不包含margin的，所以里面最好用padding替代,不然不能滑到最低部-->
			<bscroll v-else>
				<ul>
					<li v-for="item in cinemas" :key="item.id" class="smillbox">
						<div class="leftImg" @tap="handlehandle(item.id)"><img :src="item.images.small" style="width: 100px;height:130px;" /></div>
						<div class="rightText">
							<p><span id="movieTitle" @tap="handlehandle(item.id)">{{item.title | lenths}}</span><span id="average">{{item.rating.average}}</span></p>
							<p>
								导演：<b v-for="fos in item.directors">{{fos.name}}</b>
							</p>
							<p>主演：<b v-for="name in item.casts">  {{name.name}}</b></p>
							<p>类型：{{item.genres | jion}}</p>
							<p>时长：{{item.durations | jion}}</p>
							<p>上映时间：{{item.year}}</p>
						</div>
					</li>
				</ul>
			</bscroll>
		</div>
	</div>
</template>
<script>

	import hello from "@/components/HelloWorld"
  	import pag from "@/components/pag1"

	export default{
		name:"movielu",
		data(){
			return{
				cinemas:[],
				istrue:true,
				prevCityId:-1
			}
		},
		methods:{
			handlehandle(relid){
				this.$router.push('/sousuo/detail/'+relid);
			}
		},
		created(){
			const cityId = this.$store.state.city.id;
			if (this.prevCityId == cityId) {return}
				this.istrue = true;
			this.axios.get('https://douban.uieee.com/v2/movie/top250').then(res =>{
				const msg = res.statusText;
				if(msg == 'OK'){
					this.cinemas = res.data.subjects;
					this.istrue = false;
				}
			});
		},
		filters:{
			lenths(rel){
				if(rel.length > 16){
			 	   return rel.slice(0,16)+'...'
				}
				 	return rel;
			}
		},
		components:{pag,hello}
	}
</script>
<style scoped>
	*{
		margin: 0;
		padding: 0;
	}
	.crui{
		width: 100%;
		height: calc(100vh - 100px);
		overflow: hidden;
		margin-top: 50px;
		margin-bottom: 50px;
	}
	.baib{
		width: 100%;
		height: 100%;
	}
	.ab{
		border: 1px solid #97cbff;
		color: #97cbff;
		margin-right: 10px;
		padding: 2px;
		font-size: 14px;
	}
	.ba{
		border: 1px solid #ffff8f;
		color: #ffff8f;
		margin-right: 10px;
		padding: 2px;
		font-size: 14px;
	}
	.smillbox{
		margin: 0 auto;
		width: 96%;
		border-bottom: 1px solid #aaa;
		padding-bottom: 15px;
		display: flex;
	}
	.leftImg,.rightText{
		padding-top: 10px;
		padding-right: 10px;
	}
	#movieTitle{
		font-size: 16px;
		color: skyblue;
	}
	#average{
		color: yellow;
		font-size: 18px;
		padding-left: 10px;
	}
	.rightText p{
		font-size: 13px;
	}
</style>


